const mainListLinkArr = document.getElementsByClassName('main-list__link');
const mainListLink = document.querySelectorAll('.main-list__link');
const breadcrumbsTitle = document.querySelector('.product-title');
const catalogHeading = document.querySelector('.catalog-hero__heading');

const pageParams = new URLSearchParams(location.search);
const postPage = pageParams.get('heading');

if (breadcrumbsTitle) {
  breadcrumbsTitle.innerHTML = postPage;
  catalogHeading.innerHTML = postPage;
}

for (let i = 0; i < mainListLinkArr.length; i++) {
  for (let link of mainListLinkArr) {
    link.setAttribute('href', `catalog.html?heading=${postPage}`)
  }
}

function setHeading() {
  this.setAttribute('href', `catalog.html?heading=${this.innerHTML.trim()}`);
}

mainListLink.forEach(e => e.addEventListener('click', setHeading));

