// filter
const boilerBlock = document.querySelector('.catalog-hero-status__block--boiler');
const partsBlock = document.querySelector('.catalog-hero-status__block--parts');
const resetFilters = document.querySelector('.filter-reset');
const resetFiltersAll = document.querySelectorAll('.filter-reset');
const allcheckboxes = document.querySelectorAll('.checkbox');
const checkboxBoiler = document.querySelectorAll('.checkbox--boiler');
const checkboxParts = document.querySelectorAll('.checkbox--parts');
const filterItem = document.querySelectorAll('.catalog-hero-status__item');
const filterItemList = document.querySelector('.catalog-hero-status__list--boiler');
const resetFilters3 = document.querySelector('.catalog-hero-status__reset-btn--mobile');
const catalogFilterBtn = document.querySelector('.catalog-hero-status__filter-btn');
const catalogContentRight = document.querySelector('.catalog-hero-content__right');
const catalogTitleMobile = document.querySelector('.catalog-hero-content__title--mobile');
const catalogContentInner = document.querySelector('.catalog-hero-content__inner');
const body = document.querySelector('body');
let filtrItemsArr;

if (catalogFilterBtn) {
  catalogFilterBtn.addEventListener('click', () => {
  catalogContentRight.classList.toggle('mobile-filter-active');
  catalogContentInner.classList.toggle('mobile-filter-active');
  body.classList.toggle('mobile-filter-active');
 });

 catalogTitleMobile.addEventListener('click', () => {
  catalogContentRight.classList.remove('mobile-filter-active');
  catalogContentInner.classList.remove('mobile-filter-active');
  body.classList.remove('mobile-filter-active');
 });

 catalogContentInner.onclick = (e) => {
  if (e.target == catalogContentInner) {
    catalogContentInner.classList.remove('mobile-filter-active');
    catalogContentRight.classList.remove('mobile-filter-active');
    body.classList.remove('mobile-filter-active');
  }
}
}



if (resetFilters) {
  function resetFilter(btn, checkbox) {
    btn.forEach(e => {
      e.addEventListener('click', () => {

        btn.forEach(e => {
          e.disabled = true;
          e.classList.remove('catalog-hero-status__reset-btn--hover');
        })

        boilerBlock.style.display = 'none';
        partsBlock.style.display = 'none';

        filterItem.forEach(e => {
          e.classList.remove('catalog-hero-status__item--active');
        })

        checkbox.forEach(e => {
          e.classList.remove('boiler');
          e.classList.remove('parts');
        });

        checkbox.forEach(e => {
          e.checked = false;
          e.classList.remove('checked');
        });
    });
    })
  }

  resetFilter(resetFiltersAll, allcheckboxes);
}

function checkedCheckbox() {
  const checkedArr = document.getElementsByClassName('checked');
  console.log(checkedArr);

  this.classList.toggle('checked');
  resetFiltersAll.forEach(el => {
    el.disabled = false;
    el.classList.add('catalog-hero-status__reset-btn--hover');
  });


  if (checkedArr.length == 0) {
    resetFiltersAll.forEach(el => {
      el.disabled = true;
      el.classList.remove('catalog-hero-status__reset-btn--hover');
    });
  }
};

function filterByArr(array, className, element) {
  array = document.getElementsByClassName(className);

  if (array.length == 0) {
    element.style.display = 'none';
  }
}

function filterBoiler() {
  const boilerArr = [];

  this.classList.toggle('boiler');
  boilerBlock.style.display = 'block';

  filterByArr(boilerArr, 'boiler', boilerBlock);
}

function filterParts() {
  const partsArr = [];

  this.classList.toggle('parts');
  partsBlock.style.display = 'block';

  console.log(partsArr);
  filterByArr(partsArr, 'parts', partsBlock);
}

document.querySelectorAll('.checkbox--boiler').forEach(function (e) {
  e.addEventListener('click', function (e) {
    const checkItem = e.currentTarget.dataset.boiler;
    filtrItemsArr = document.getElementsByClassName('catalog-hero-status__item--active');
    console.log(filtrItemsArr);

    document.querySelectorAll('.catalog-hero-status__item').forEach(function (e) {
      document.querySelector(`[data-target='${checkItem}']`).classList.toggle('catalog-hero-status__item--active');
    });
  });
});

document.querySelectorAll('.checkbox--parts').forEach(function (e) {
  e.addEventListener('click', function (e) {
    const checkItem = e.currentTarget.dataset.parts;
    filtrItemsArr = document.getElementsByClassName('catalog-hero-status__item--active');
    console.log(filtrItemsArr);

    document.querySelectorAll('.catalog-hero-status__item').forEach(function (e) {
      document.querySelector(`[data-target='${checkItem}']`).classList.toggle('catalog-hero-status__item--active');
    });
  });
});

function removeFilterItem(key) {
  document.querySelectorAll(`.close--${key}`).forEach(function (e) {
    e.addEventListener('click', function (e) {

      if (filtrItemsArr.length == 1) {
        resetFilters.disabled = true;
        resetFilters.classList.remove('catalog-hero-status__reset-btn--hover');
        boilerBlock.style.display = 'none';
        partsBlock.style.display = 'none';
      }
      const closeItem = e.currentTarget.dataset.close;

      document.querySelectorAll('.catalog-hero-status__item').forEach(function (e) {
        document.querySelector(`[data-target='${closeItem}']`).classList.remove('catalog-hero-status__item--active');
      });

      document.querySelectorAll('.checkbox--boiler').forEach(function (e) {
        document.querySelector(`[data-${key}='${closeItem}']`).checked = false;
        document.querySelector(`[data-${key}='${closeItem}']`).classList.remove(`${key}`);
        document.querySelector(`[data-${key}='${closeItem}']`).classList.remove('checked');
      });
    });
  });
}
removeFilterItem('boiler');
removeFilterItem('parts');

checkboxParts.forEach(e => e.addEventListener('click', filterParts));
checkboxBoiler.forEach(e => e.addEventListener('click', filterBoiler));
allcheckboxes.forEach(e => e.addEventListener('click', checkedCheckbox));


