  // cart-display
  const cartDisplayClose = document.querySelector('.cart-display__close');
  const cartDisplay = document.querySelector('.cart-display');
  const cartDisplayCount = document.querySelector('.cart-display-count__count');

  if (cartDisplay) {
    if (cartDisplayCount.innerHTML == 0) {
      cartDisplay.classList.add('is-hidden');
  } else {
      cartDisplay.classList.remove('is-hidden');
  }

  cartDisplayClose.addEventListener('click', function() {
      cartDisplay.classList.add('is-hidden');
  });
  }
