const accordions = document.querySelectorAll('.footer-accordion__item');
const accordions2 = document.querySelectorAll('.catalog-hero-accordion');
const accordionTitle = document.querySelectorAll('.catalog-hero-accordion__title');

	accordions.forEach(el => {
		el.addEventListener('click', (e) => {
			const self = e.currentTarget;
			const control = self.querySelector('.footer-accordion__control');
			const content = self.querySelector('.footer-accordion__content');

			self.classList.toggle('open');

			// если открыт аккордеон
			if (self.classList.contains('open')) {
				control.setAttribute('aria-expanded', true);
				content.setAttribute('aria-hidden', false);
				content.style.maxHeight = content.scrollHeight + 'px';
			} else {
				control.setAttribute('aria-expanded', false);
				content.setAttribute('aria-hidden', true);
				content.style.maxHeight = null;
			}
		});
	});

//accordion
if (window.innerWidth > 1150) {

$('#accordion').accordion({
  collapsible:true,
  heightStyle: "content",
  active: false,

  beforeActivate: function(event, ui) {
       // The accordion believes a panel is being opened
      if (ui.newHeader[0]) {
          var currHeader  = ui.newHeader;
          var currContent = currHeader.next('.ui-accordion-content');
       // The accordion believes a panel is being closed
      } else {
          var currHeader  = ui.oldHeader;
          var currContent = currHeader.next('.ui-accordion-content');
      }
       // Since we've changed the default behavior, this detects the actual status
      var isPanelSelected = currHeader.attr('aria-selected') == 'true';

       // Toggle the panel's header
      currHeader.toggleClass('ui-corner-all',isPanelSelected).toggleClass('accordion-header-active ui-state-active ui-corner-top',!isPanelSelected).attr('aria-selected',((!isPanelSelected).toString()));

      // Toggle the panel's icon
      currHeader.children('.ui-icon').toggleClass('ui-icon-triangle-1-e',isPanelSelected).toggleClass('ui-icon-triangle-1-s',!isPanelSelected);

       // Toggle the panel's content
      currContent.toggleClass('accordion-content-active',!isPanelSelected)
      if (isPanelSelected) { currContent.slideUp(); }  else { currContent.slideDown(); }

      return false; // Cancel the default action
  }
});
}

const titleBoiler = document.querySelector('.accordion__title--boiler');
const titleParts = document.querySelector('.accordion__title--parts');
const contentBoiler = document.querySelector('.accordion__content--boiler');
const contentParts = document.querySelector('.accordion__content--parts');
const titleMobile = document.querySelectorAll('.accordion__title--mobile');

titleMobile.forEach(e => {
  e.addEventListener('click', () => {
    contentBoiler.classList.remove('accordion__content--active');
    contentParts.classList.remove('accordion__content--active');
  })

});

if (titleBoiler) {
  titleBoiler.addEventListener('click', () => {
  contentBoiler.classList.toggle('accordion__content--active');
});

titleParts.addEventListener('click', () => {
  contentParts.classList.toggle('accordion__content--active');
});
}







