const productList = document.querySelector('.product-list');
const loadMore = document.querySelector('.load-more__btn');
const loadMoreIcon = document.querySelector('.load-more__load');
let prodQuantity = 24;
let dataLength = null;
let timeoutId;

const normalPrice = (str) => {
  return String(str).replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1 ');
};

if (productList) {
  const loadProducts = (quantity) => {
    fetch('../data/data.json')
    .then((response) => {
      return response.json();
    })
    .then((data) => {
      dataLength = data.length;

      productList.innerHTML = '';
      for (let i = 0; i < dataLength; i++) {
        if (i < quantity) {
          let item = data[i];
          console.log(item);

          productList.innerHTML += `
          <li class="product-list__item">
            <article class="product-list__article hits-slide">
              <img src="${item.mainImage}" alt="${item.title}">
              <div class="hits-slide__inner product-list__inner">
                <h3 class="product-list__title"><a href="#"
                    class="hits-slide__name product-list__name">${item.title}</a></h3>
                <span class="hits-slide__vendor product-list__vendor">Артикул: 354715946</span>
                <span class="hits-slide__price product-list__price">${normalPrice(item.price)} р</span>
              </div>
              <button class="product-list__status btn-reset">
                <span class="product-list__add"></span>
                <span class="product-list__added"></span>
              </button>
            </article>
          </li>
          `;
        }
      }

      const cartStatusBtn = document.querySelectorAll('.product-list__status');

      cartStatusBtn.forEach(el => {
        el.addEventListener('click', (e) => {
         const self = e.currentTarget;

         const addToCart = self.querySelector('.product-list__add');
         const addedToCart = self.querySelector('.product-list__added');

         self.classList.toggle('csrt-status-change');

         if (self.classList.contains('csrt-status-change')) {
           addToCart.style.display = 'none';
           addedToCart.style.display = 'block';
         } else {
           addToCart.style.display = 'block';
           addedToCart.style.display = 'none';
         }
       })
     })
  })}
  loadProducts(prodQuantity);

  loadMore.addEventListener('click', (e) => {
    prodQuantity = prodQuantity + 4;

    clearTimeout(timeoutId);

    timeoutId = setTimeout( () => {
      loadProducts(prodQuantity);
      loadMoreIcon.style.animationName = 'ball_moveGg';
    }, 4000);

    if (prodQuantity >= dataLength) {
      loadMore.style.display = 'none';
      loadMoreIcon.style.display = 'none';
    } else {
      loadMore.style.display = 'block';
      loadMoreIcon.style.display = 'block';
      loadMoreIcon.style.animationName = 'ball_moveG';
    }
  });
}
