const mainListLink = document.querySelectorAll('.main-list__link');
const catalogHeroHeading = document.querySelector('.catalog-hero__heading');
const productTitle = document.querySelector('.product-title');

function showProductHeading() {
  catalogHeroHeading.innerHTML = this.innerHTML;
  productTitle.innerHTML = this.innerHTML;
};

mainListLink.forEach(e => e.addEventListener('click', showProductHeading));
