 // slider-hero
 var swiper = new Swiper('.hero__slider', {
  slidesPerView: 'auto',
  spaceBetween: 16,
  freeMode: true,
  navigation: {
    nextEl: '.hero-pag-1',
    prevEl: '.hero-pag-2',
  }
});

 // slider-hits
 var swiper = new Swiper('.hits__slider', {
  navigation: {
    nextEl: '.hits-pag-1',
    prevEl: '.hits-pag-2',
  },
  breakpoints: {
    320: {
      slidesPerView: 'auto',
      spaceBetween: 8,
    },

    480: {
      slidesPerView: 'auto',
      spaceBetween: 16,
    },
    750: {
      slidesPerView: 'auto',
      spaceBetween: 16,
    },
    1150: {
      slidesPerView: 3,
      spaceBetween: 16
    },
    // when window width is >= 640px
    1450: {
      slidesPerView: 4,
      spaceBetween: 16,
    }
  }
});
